/* ------------------------------

custom slider

---------------------------------*/

.slider-progress{

	position:absolute;

	top:0;

	left:0;

	background-color: rgba(255, 255, 255, .7);

	height:5px;

	-webkit-animation: myfirst 5000ms ease-in-out;

	-moz-animation: myfirst 5000ms ease-in-out;

	-ms-animation: myfirst 5000ms ease-in-out;

	animation: myfirst 5000ms ease-in-out;

}

